@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
  background-color: rgb(245, 244, 244);
}

body {
  line-height: 1;
  font-family: "";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
.App {
  max-width: 100%;
  width: 100%;
}
.material-icons {
  line-height: normal;
}

.NewPost{max-width:700px;width:100%;display:flex;flex-direction:column;margin:0 auto;margin-top:30px;padding:15px;border:1px solid black;border-radius:10px}.NewPost-Title{width:100%;display:flex;justify-content:space-between}.NewPost-Title-item{display:flex;align-items:center}.NewPost-form{width:100%;margin-top:20px;display:flex;flex-direction:column;align-items:flex-end}.textarea{width:calc(100% - 20px);height:70px;border:1px solid black;border-radius:2px;margin:10px;padding:10px;font-size:16px;cursor:pointer}.material-icons{line-height:normal}.send{text-align:right;padding:5px;background-color:#8080db;color:white;line-height:normal !important;padding-left:15px;padding-right:15px;border-radius:5px;font-size:20px;cursor:pointer}.close{color:red;cursor:pointer}

.Post-Link{text-decoration:none;color:black;margin-bottom:10px}.Post-Link:focus,.Post-Link:hover,.Post-Link:visited,.Post-Link:link,.Post-Link:active{text-decoration:none}.Post{width:100%;max-width:100%;border:1px solid black;border-radius:10px;padding:15px;margin-bottom:10px;background-color:white}.Post-Link:last-child{margin-bottom:0px}.Post__title{display:flex;align-items:center;margin-bottom:10px}.title__ava{max-width:50px;width:100%;margin-right:20px}.name{margin-bottom:7px}.Post__body{margin-left:20px;font-size:24px;margin-bottom:15px}.Post-footer{display:flex;justify-content:space-between;padding-left:30px;padding-right:30px}.Post-footer__item{display:flex;align-items:center}.material-icons{margin-right:10px}

.ListPost{max-width:700px;width:100%;margin:0 auto;margin-top:30px;display:flex;flex-direction:column;justify-content:center;align-items:center}.ListPost-title{width:100%;display:flex;justify-content:flex-end;background-color:white;padding:10px}.btn{line-height:normal;padding:5px;border-radius:4px;background-color:#8f8fd4;color:white;padding-left:15px;padding-right:15px}.ListPost-row{width:100%;padding:10px}

.ViewPost-controll{display:flex;justify-content:right;margin-top:20px}.btn{text-align:left;padding:5px;background-color:#8080db;color:white;line-height:normal;padding-left:15px;padding-right:15px;border-radius:5px;font-size:20px;cursor:pointer;margin-left:20px}.delite{background-color:#e62929}

